/* Global Styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --navbar-height: 80px;
  --navbar-height-scrolled: 70px;
  --primary-color: #4A6CFD;
  --primary-light: rgba(74, 108, 253, 0.1);
  --primary-lighter: rgba(74, 108, 253, 0.05);
  --bg-light: #fdfdff;
  --section-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: calc(var(--navbar-height) + 20px);
  overflow-x: hidden;
  width: 100%;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--bg-light);
  color: #333;
  line-height: 1.5;
  overflow-y: auto; /* Ensure vertical scrolling works properly */
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  position: relative;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  position: relative;
}

.main-content {
  flex-grow: 1;
  width: 100%;
  padding-top: var(--navbar-height); /* Add padding to account for fixed navbar */
  position: relative;
  z-index: 1;
}

/* Smooth appear animation for sections when they come into view */
.main-content section {
  display: block;
  opacity: 1;
  min-height: 200px; /* Ensure minimum height for all sections */
  transition: var(--section-transition);
  scroll-margin-top: calc(var(--navbar-height) + 20px);
}

/* Active section styling - enhanced */
section.active-section {
  position: relative;
}

section.active-section::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background: linear-gradient(135deg, #4A6CFD, #3b57db);
  z-index: 5;
  animation: pulseActiveSection 2s infinite alternate ease-in-out;
}

@keyframes pulseActiveSection {
  0% { opacity: 0.7; box-shadow: 0 0 10px rgba(74, 108, 253, 0.3); }
  100% { opacity: 1; box-shadow: 0 0 20px rgba(74, 108, 253, 0.6); }
}

/* Section highlight effect when navigated to via navbar */
section.section-highlight {
  animation: highlightSection 1.5s ease;
}

@keyframes highlightSection {
  0% { background-color: transparent; }
  25% { background-color: rgba(74, 108, 253, 0.08); }
  100% { background-color: transparent; }
}

/* Scrollbar styling - enhanced */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 6px;
  border: 3px solid #f1f1f1;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

/* Enhanced focus styles for accessibility */
a:focus, button:focus, input:focus, textarea:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 3px;
}

a:focus:not(:focus-visible), 
button:focus:not(:focus-visible), 
input:focus:not(:focus-visible), 
textarea:focus:not(:focus-visible) {
  outline: none;
}

/* Mobile optimization */
@media (max-width: 768px) {
  html {
    scroll-padding-top: calc(var(--navbar-height-scrolled) + 10px);
  }
  
  .main-content section {
    scroll-margin-top: calc(var(--navbar-height-scrolled) + 10px);
  }
}

