/* Global variables */
:root {
  --primary-color: #4A6CFD;
  --primary-light: rgba(74, 108, 253, 0.1);
  --primary-lighter: rgba(74, 108, 253, 0.05);
  --primary-dark: #3b57db;
  --primary-gradient: linear-gradient(135deg, #4A6CFD, #3b57db);
  --shadow-navbar: 0 5px 20px rgba(0, 0, 0, 0.1);
  --navbar-bg: rgba(255, 255, 255, 0.8);
  --navbar-bg-scrolled: rgba(255, 255, 255, 0.95);
  --navbar-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  --navbar-height: 80px;
  --navbar-height-scrolled: 70px;
  --navbar-transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
  --text-dark: #2d2e32;
  --text-gray: #4b5563;
  --bg-white: #ffffff;
}

/* Navbar Styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--navbar-height);
  background: var(--navbar-bg);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: 1000;
  transition: var(--navbar-transition);
  border-bottom: 1px solid rgba(0, 0, 0, 0.03);
}

.navbar.scrolled {
  height: var(--navbar-height-scrolled);
  background: var(--navbar-bg-scrolled);
  box-shadow: var(--navbar-shadow);
  border-bottom: none;
}

.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  height: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.navbar-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  position: relative;
  transition: var(--navbar-transition);
}

.logo-text {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-dark);
  transition: var(--navbar-transition);
}

.navbar.scrolled .logo-text {
  font-size: 1.6rem;
}

.logo-dot {
  width: 8px;
  height: 8px;
  background: var(--primary-gradient);
  border-radius: 50%;
  margin-left: 3px;
  position: relative;
  top: -8px;
  transition: var(--navbar-transition);
  box-shadow: 0 0 10px rgba(74, 108, 253, 0.5);
}

.navbar.scrolled .logo-dot {
  width: 6px;
  height: 6px;
}

.navbar-logo:hover .logo-text {
  color: var(--primary-color);
}

.navbar-logo:hover .logo-dot {
  transform: scale(1.3);
  box-shadow: 0 0 15px rgba(74, 108, 253, 0.7);
}

.menu-items {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
}

.menu-items li {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

.menu-items a {
  color: var(--text-gray);
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  padding: 0 20px;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  transition: var(--navbar-transition);
}

.menu-items a::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 3px;
  background: var(--primary-gradient);
  transform: translateX(-50%);
  transition: width 0.3s ease, opacity 0.3s ease, box-shadow 0.3s ease;
  border-radius: 3px 3px 0 0;
  opacity: 0;
}

.menu-items a:hover {
  color: var(--primary-color);
}

.menu-items a:hover::before {
  width: 30%;
  opacity: 0.7;
}

.menu-items a.active {
  color: var(--primary-color);
  font-weight: 600;
}

.menu-items a.active::before {
  width: 60%;
  opacity: 1;
  box-shadow: 0 0 10px rgba(74, 108, 253, 0.3);
}

/* New active indicator styles */
.active-indicator {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--primary-gradient);
  top: 40%;
  left: 5px;
  box-shadow: 0 0 10px rgba(74, 108, 253, 0.5);
  animation: pulseIndicator 2s infinite alternate ease-in-out;
}

@keyframes pulseIndicator {
  0% { transform: scale(1); opacity: 0.8; }
  100% { transform: scale(1.5); opacity: 1; }
}

/* Improved active link animation */
@keyframes pulseActive {
  0% { box-shadow: 0 0 0 0 rgba(74, 108, 253, 0.4); }
  70% { box-shadow: 0 0 0 6px rgba(74, 108, 253, 0); }
  100% { box-shadow: 0 0 0 0 rgba(74, 108, 253, 0); }
}

.menu-items a.active::before {
  animation: pulseActive 2s infinite;
}

/* Highlight active section in navbar when scrolled to it */
.menu-items a.active::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--primary-lighter);
  z-index: -1;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.menu-items a.active:hover::after {
  opacity: 0.8;
}

/* Hamburger menu styling for mobile */
.menu-icon {
  display: none;
  cursor: pointer;
  height: 40px;
  width: 40px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
  background-color: transparent;
  z-index: 100;
}

.menu-icon:hover {
  background-color: var(--primary-lighter);
}

.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 20px;
  width: 30px;
  position: relative;
}

.hamburger span {
  display: block;
  height: 3px;
  width: 100%;
  background: var(--text-dark);
  border-radius: 3px;
  transition: all 0.3s ease;
  transform-origin: left center;
}

.hamburger.active span:nth-child(1) {
  transform: rotate(45deg);
  width: 110%;
  background: var(--primary-color);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
  transform: translateX(-10px);
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg);
  width: 110%;
  background: var(--primary-color);
}

/* Body class for preventing scroll when mobile menu is open */
body.menu-open {
  overflow: hidden;
}

/* Enhanced responsive styles */
@media (max-width: 992px) {
  .menu-icon {
    display: flex;
  }
  
  .menu-items {
    position: fixed;
    top: var(--navbar-height);
    right: -100%;
    width: 300px;
    height: calc(100vh - var(--navbar-height));
    background: var(--bg-white);
    flex-direction: column;
    align-items: flex-start;
    padding: 30px 0;
    box-shadow: -5px 0 20px rgba(0, 0, 0, 0.1);
    transition: right 0.4s cubic-bezier(0.23, 1, 0.32, 1);
    overflow-y: auto;
    z-index: 99;
  }
  
  .navbar.scrolled .menu-items {
    top: var(--navbar-height-scrolled);
    height: calc(100vh - var(--navbar-height-scrolled));
  }
  
  .menu-items.active {
    right: 0;
  }
  
  .menu-items li {
    width: 100%;
    height: auto;
    margin: 10px 0;
  }
  
  .menu-items a {
    padding: 15px 30px;
    width: 100%;
    border-left: 3px solid transparent;
    transition: all 0.3s ease;
    height: auto;
  }
  
  .menu-items a::before {
    display: none;
  }
  
  .menu-items a.active {
    border-left: 3px solid var(--primary-color);
    background: var(--primary-lighter);
  }
  
  .menu-items a:hover {
    background: var(--primary-lighter);
    padding-left: 40px;
  }
  
  .menu-items a.active::after {
    display: none;
  }
  
  /* Reposition active indicator for mobile */
  .active-indicator {
    left: auto;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  /* Animation for mobile menu items */
  .menu-items.active li {
    animation: slideIn 0.4s forwards;
    opacity: 0;
    transform: translateX(30px);
  }
  
  @keyframes slideIn {
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  .menu-items.active li:nth-child(1) { animation-delay: 0.1s; }
  .menu-items.active li:nth-child(2) { animation-delay: 0.2s; }
  .menu-items.active li:nth-child(3) { animation-delay: 0.3s; }
  .menu-items.active li:nth-child(4) { animation-delay: 0.4s; }
  .menu-items.active li:nth-child(5) { animation-delay: 0.5s; }
}

@media (max-width: 480px) {
  .navbar-container {
    padding: 0 20px;
  }
  
  .logo-text {
    font-size: 1.6rem;
  }
  
  .navbar.scrolled .logo-text {
    font-size: 1.4rem;
  }
  
  .menu-items {
    width: 260px;
  }
  
  .menu-items a {
    padding: 12px 25px;
    font-size: 0.95rem;
  }
  
  .menu-items a:hover {
    padding-left: 35px;
  }
} 