/* Color palette - Modern expanded color scheme */
:root {
  --primary-color: #4A6CFD;
  --primary-light: rgba(74, 108, 253, 0.1);
  --primary-lighter: rgba(74, 108, 253, 0.05);
  --primary-dark: #3b57db;
  --primary-gradient: linear-gradient(135deg, #4A6CFD, #3b57db);
  --secondary-color: #00c8ff;
  --secondary-light: rgba(0, 200, 255, 0.1);
  --accent-color: #ff7e5f;
  --accent-gradient: linear-gradient(135deg, #ff7e5f, #feb47b);
  --text-dark: #2d2e32;
  --text-gray: #4b5563;
  --text-light: #6b7280;
  --bg-light: #fdfdff;
  --bg-white: #ffffff;
  --shadow-sm: 0 5px 15px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 10px 30px rgba(0, 0, 0, 0.07);
  --shadow-lg: 0 15px 40px rgba(0, 0, 0, 0.1);
  --border-light: rgba(0, 0, 0, 0.05);
  --border-medium: rgba(0, 0, 0, 0.1);
}

/* General Styles */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 80px; /* Adjust for fixed navbar height */
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--bg-light);
  color: var(--text-dark);
  line-height: 1.7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: auto;
}

/* Create a full-width container that adapts to viewport */
.home-container {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}

/* Add subtle background patterns */
.home-container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 10% 20%, var(--primary-lighter) 0%, transparent 20%),
    radial-gradient(circle at 90% 80%, var(--primary-lighter) 0%, transparent 20%),
    radial-gradient(circle at 50% 50%, var(--primary-lighter) 0%, transparent 40%);
  z-index: -1;
  opacity: 0.7;
}

/* All main sections get full width background */
section, footer {
  width: 100%;
  padding: 90px 0;
  box-sizing: border-box;
  margin-top: 20px;
  display: block;
  overflow: visible;
  position: relative;
  z-index: 1;
  transition: background-color 0.5s ease;
}

/* Active section styling */
section.active-section, 
footer.active-section {
  position: relative;
  background-color: rgba(74, 108, 253, 0.02);
}

/* Side indicator for active section */
section.active-section::before, 
footer.active-section::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background: var(--primary-gradient);
  box-shadow: 0 0 15px rgba(74, 108, 253, 0.5);
  animation: pulseActiveMarker 2s infinite alternate ease-in-out;
}

@keyframes pulseActiveMarker {
  0% { opacity: 0.7; box-shadow: 0 0 10px rgba(74, 108, 253, 0.3); }
  100% { opacity: 1; box-shadow: 0 0 20px rgba(74, 108, 253, 0.6); }
}

/* Section highlight when clicked from navbar */
section.section-highlight,
footer.section-highlight {
  animation: highlightPulse 1.5s ease;
}

@keyframes highlightPulse {
  0% { background-color: transparent; }
  30% { background-color: rgba(74, 108, 253, 0.12); }
  70% { background-color: rgba(74, 108, 253, 0.08); }
  100% { background-color: transparent; }
}

/* Content container for consistent width on desktop */
section > *, .footer-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 30px;
  box-sizing: border-box;
  position: relative;
}

/* ==================== HERO SECTION STYLES ==================== */
.hero-section {
  min-height: 100vh;
  padding: 100px 0 80px;
  margin-top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: var(--bg-light);
  overflow: hidden;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
}

/* Animated background shapes */
.animated-shapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.shape {
  position: absolute;
  background: var(--primary-light);
  border-radius: 50%;
}

.shape-1 {
  width: 350px;
  height: 350px;
  top: -100px;
  right: -150px;
  opacity: 0.3;
  animation: floatAnimation 20s infinite alternate ease-in-out;
}

.shape-2 {
  width: 250px;
  height: 250px;
  bottom: -50px;
  left: -100px;
  opacity: 0.2;
  animation: floatAnimation 25s infinite alternate-reverse ease-in-out;
}

.shape-3 {
  width: 150px;
  height: 150px;
  top: 20%;
  left: 10%;
  opacity: 0.15;
  animation: floatAnimation 15s infinite alternate ease-in-out;
}

.shape-4 {
  width: 200px;
  height: 200px;
  bottom: 10%;
  right: 10%;
  opacity: 0.2;
  animation: floatAnimation 18s infinite alternate-reverse ease-in-out;
}

@keyframes floatAnimation {
  0% { transform: translate(0, 0) rotate(0deg); }
  50% { transform: translate(20px, 20px) rotate(5deg); }
  100% { transform: translate(-20px, -20px) rotate(-5deg); }
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Floating particles */
.floating-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.particle {
  position: absolute;
  background: var(--primary-color);
  border-radius: 50%;
  opacity: 0.2;
}

.particle-1 {
  width: 10px;
  height: 10px;
  top: 20%;
  left: 20%;
  animation: particleFloat 20s infinite linear;
}

.particle-2 {
  width: 15px;
  height: 15px;
  top: 70%;
  left: 80%;
  animation: particleFloat 25s infinite linear;
}

.particle-3 {
  width: 8px;
  height: 8px;
  top: 40%;
  left: 40%;
  animation: particleFloat 15s infinite linear;
}

.particle-4 {
  width: 12px;
  height: 12px;
  top: 80%;
  left: 15%;
  animation: particleFloat 22s infinite linear;
}

.particle-5 {
  width: 6px;
  height: 6px;
  top: 30%;
  left: 85%;
  animation: particleFloat 18s infinite linear;
}

@keyframes particleFloat {
  0% { transform: translateY(0) translateX(0); }
  25% { transform: translateY(100px) translateX(50px); }
  50% { transform: translateY(0) translateX(100px); }
  75% { transform: translateY(-100px) translateX(50px); }
  100% { transform: translateY(0) translateX(0); }
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Hero content layout - updated for full width */
.hero-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 30px;
  position: relative;
  z-index: 2;
  min-height: 85vh;
}

.hero-content-left {
  flex: 1;
  max-width: 800px;
  animation: fadeInUp 1s ease;
  text-align: center;
  margin: 0 auto;
}

.hero-text {
  padding: 0;
}

/* Enhanced typography for hero */
.hero-content-left h1 {
  font-size: 4.5rem;
  font-weight: 800;
  line-height: 1.2;
  margin: 0 0 30px;
  position: relative;
  z-index: 1;
}

.hero-subtitle {
  font-size: 1.25rem;
  color: var(--text-gray);
  margin-bottom: 40px;
  max-width: 700px;
  line-height: 1.8;
  margin-left: auto;
  margin-right: auto;
}

/* Hero stats styling */
.hero-stats {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.stat-item:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -20px;
  top: 50%;
  transform: translateY(-50%);
  height: 40px;
  width: 1px;
  background: var(--border-medium);
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-gray);
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Hero buttons - centered */
.hero-buttons {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
  justify-content: center;
}

/* Primary button style */
.primary-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 35px;
  font-size: 1rem;
  font-weight: 600;
  background: var(--primary-gradient);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 15px rgba(74, 108, 253, 0.2);
  text-decoration: none;
  position: relative;
  overflow: hidden;
  transform-origin: center;
  transform: scale(1);
}

.primary-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, var(--primary-dark), var(--primary-color));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.primary-button:hover, .primary-button:focus {
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 8px 25px rgba(74, 108, 253, 0.4);
  outline: none;
}

.primary-button:hover::before {
  opacity: 1;
}

.primary-button span {
  position: relative;
  z-index: 1;
}

/* Secondary button style */
.secondary-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 35px;
  font-size: 1rem;
  font-weight: 600;
  background: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  transform-origin: center;
  transform: scale(1);
}

.secondary-button:hover, .secondary-button:focus {
  background-color: var(--primary-light);
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 8px 20px rgba(74, 108, 253, 0.2);
  outline: none;
}

.secondary-button span {
  position: relative;
  z-index: 1;
}

/* Social icons - centered */
.social-icons {
  display: flex;
  gap: 15px;
  margin-top: 30px;
  justify-content: center;
}

.social-icons a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--bg-white);
  box-shadow: var(--shadow-sm);
  position: relative;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  overflow: hidden;
  margin: 0 5px;
}

.social-icons a::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--primary-gradient);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 0;
}

.social-icons a:hover {
  transform: translateY(-8px) rotate(8deg);
  box-shadow: var(--shadow-md);
}

.social-icons a:hover::before {
  opacity: 1;
}

.social-icons svg {
  width: 24px;
  height: 24px;
  position: relative;
  z-index: 1;
  color: var(--primary-color);
  transition: color 0.3s ease;
}

.social-icons a:hover svg {
  color: white;
}

/* Scroll animations have been removed as they are no longer needed */

/* About Section - Consistent styling */
.about-section {
  background-color: var(--bg-white);
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  display: block;
  border-top: 1px solid var(--border-light);
  margin-top: 0;
  position: relative;
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Add subtle background pattern to about section */
.about-section::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  height: 300px;
  background: var(--primary-lighter);
  border-radius: 20% 80% 50% 50% / 50% 40% 60% 50%;
  opacity: 0.7;
  z-index: 0;
  animation: floatAnimation 20s infinite alternate ease-in-out;
}

.about-section::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 250px;
  height: 250px;
  background: var(--primary-lighter);
  border-radius: 50% 50% 30% 70% / 40% 60% 40% 60%;
  opacity: 0.5;
  z-index: 0;
  animation: floatAnimation 18s infinite alternate-reverse ease-in-out;
}

/* Section headings */
.about-section h2, 
.skills-section h2, 
.works-section h2, 
.contact-section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: 50px;
  position: relative;
  text-align: center;
  display: block;
  width: 100%;
  z-index: 1;
}

.about-section h2::after, 
.skills-section h2::after, 
.works-section h2::after, 
.contact-section h2::after {
  content: '';
  position: absolute;
  width: 80px;
  height: 5px;
  background: var(--primary-gradient);
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 3px;
  box-shadow: 0 2px 10px rgba(74, 108, 253, 0.2);
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 30px;
  position: relative;
  z-index: 1;
}

.about-text {
  flex: 1;
  max-width: 800px;
  text-align: center;
}

.about-text h3 {
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: 30px;
  position: relative;
  display: inline-block;
}

.about-text h3::after {
  content: '';
  position: absolute;
  width: 60px;
  height: 3px;
  background: var(--primary-light);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
}

.about-text p {
  font-size: 1.1rem;
  margin-bottom: 25px;
  color: var(--text-gray);
  line-height: 1.8;
  max-width: 700px;
}

.about-cta {
  margin-top: 20px;
}

/* Skills Section - Alternating background */
.skills-section {
  background-color: var(--bg-light);
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  display: block;
  border-top: 1px solid var(--border-light);
  margin-top: 0;
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Add subtle background pattern to skills section */
.skills-section::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 10%;
  width: 250px;
  height: 250px;
  background: var(--primary-lighter);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  opacity: 0.6;
  z-index: 0;
  transform: translateY(-50%);
  animation: floatAnimation 22s infinite alternate ease-in-out;
}

.skills-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  position: relative;
  z-index: 1;
}

.skill-card {
  background: var(--bg-white);
  border-radius: 16px;
  padding: 35px 25px;
  box-shadow: var(--shadow-md);
  transition: all 0.6s cubic-bezier(0.34, 1.56, 0.64, 1);
  position: relative;
  overflow: hidden;
  z-index: 1;
  border: 1px solid var(--border-light);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transform-style: preserve-3d;
  transform: translateY(0);
  animation: floatSkillCard 5s infinite alternate ease-in-out;
}

.skill-card:nth-child(1) {
  animation-delay: 0s;
}

.skill-card:nth-child(2) {
  animation-delay: 0.3s;
}

.skill-card:nth-child(3) {
  animation-delay: 0.6s;
}

@keyframes floatSkillCard {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-8px);
  }
}

.skill-card:hover {
  animation-play-state: paused;
  transform: translateY(-15px) scale(1.03);
  box-shadow: 0 15px 35px rgba(74, 108, 253, 0.2);
  border-color: var(--primary-color);
}

.skill-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--primary-gradient);
  transform: translateY(-5px);
  transition: transform 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  z-index: 1;
}

.skill-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transform: translateY(-100%);
  transition: transform 0.8s cubic-bezier(0.34, 1.56, 0.64, 1);
  z-index: 0;
  pointer-events: none;
}

.skill-card:hover::before {
  transform: translateY(0);
}

.skill-card:hover::after {
  transform: translateY(100%);
}

.skill-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 30px;
  position: relative;
  color: var(--text-dark);
  text-align: center;
  transition: color 0.5s ease, transform 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.skill-card:hover h3 {
  color: var(--primary-color);
  transform: scale(1.05);
}

.skill-card h3::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 3px;
  background: var(--primary-color);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%) scaleX(0.6);
  border-radius: 2px;
  opacity: 0.7;
  transition: transform 0.5s cubic-bezier(0.34, 1.56, 0.64, 1), opacity 0.3s ease, background 0.3s ease, width 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.skill-card:hover h3::after {
  transform: translateX(-50%) scaleX(1);
  opacity: 1;
  background: var(--primary-gradient);
  width: 60px;
}

.skill-card ul {
  list-style: none;
  padding: 0;
  margin: 0;
  padding-top: 10px;
  width: 100%;
  text-align: left;
  padding-left: 20px;
  position: relative;
  z-index: 2;
}

.skill-card li {
  margin-bottom: 15px;
  padding-left: 30px;
  position: relative;
  color: var(--text-gray);
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1), color 0.3s ease, padding-left 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.skill-card:hover li {
  color: var(--text-dark);
}

.skill-card li:hover {
  transform: translateX(8px);
  color: var(--primary-color);
  padding-left: 35px;
}

.skill-card li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--primary-color);
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1), background 0.3s ease, box-shadow 0.3s ease;
}

.skill-card li:hover::before {
  transform: scale(1.5);
  background: var(--primary-gradient);
  box-shadow: 0 0 10px rgba(74, 108, 253, 0.5);
}

/* Enhanced animation for skill cards */
.scale-in {
  opacity: 0;
  transform: scale(0.9) translateY(30px);
  transition: opacity 0.8s cubic-bezier(0.34, 1.56, 0.64, 1), 
              transform 0.8s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}

.scale-in.is-visible {
  opacity: 1;
  transform: scale(1) translateY(0);
}

/* Works Section - Consistent styling */
.works-section {
  background-color: var(--bg-white);
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  display: block;
  border-top: 1px solid var(--border-light);
  margin-top: 0;
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  perspective: 1000px;
}

/* Add subtle background pattern to works section */
.works-section::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 350px;
  height: 350px;
  background: var(--primary-lighter);
  border-radius: 40% 60% 30% 70% / 40% 40% 60% 50%;
  opacity: 0.6;
  z-index: 0;
  animation: floatAnimation 25s infinite alternate-reverse ease-in-out;
}

.works-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 40px;
  position: relative;
  z-index: 1;
  transform-style: preserve-3d;
}

.work-item-placeholder {
  background: var(--bg-white);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: var(--shadow-md);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  z-index: 1;
  border: 1px solid var(--border-light);
  height: 100%;
  transform-style: preserve-3d;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}

.work-item-placeholder:hover {
  transform: translateY(-20px) scale(1.05);
  box-shadow: 0 20px 50px rgba(74, 108, 253, 0.25);
  border-color: var(--primary-color);
}

.work-item-placeholder::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transform: translateY(-100%);
  transition: transform 0.6s ease;
  z-index: 2;
  pointer-events: none;
}

.work-item-placeholder::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 6px;
  background: var(--primary-gradient);
  bottom: 0;
  left: 0;
  transform: translateY(6px);
  transition: transform 0.4s ease;
}

.work-item-placeholder:hover::before {
  transform: translateY(100%);
}

.work-item-placeholder:hover::after {
  transform: translateY(0);
}

.work-item-image-placeholder {
  height: 220px;
  background: linear-gradient(135deg, var(--primary-lighter), var(--secondary-light));
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease;
  width: 100%;
}

.work-item-image-placeholder::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, transparent 60%, rgba(0, 0, 0, 0.25) 100%);
  opacity: 0.8;
  transition: opacity 0.5s ease;
}

.work-item-placeholder:hover .work-item-image-placeholder::after {
  opacity: 0.5;
}

.work-item-placeholder:hover .work-item-image-placeholder {
  transform: scale(1.05);
}

.work-item-placeholder h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 25px 20px 15px;
  color: var(--text-dark);
  text-align: center;
  position: relative;
  transition: color 0.3s ease;
}

.work-item-placeholder:hover h3 {
  color: var(--primary-color);
}

.work-item-placeholder h3::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 3px;
  background: var(--primary-gradient);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transition: transform 0.4s ease;
  border-radius: 3px;
}

.work-item-placeholder:hover h3::after {
  transform: translateX(-50%) scaleX(1);
}

.work-item-placeholder p {
  font-size: 1rem;
  margin: 15px 20px;
  color: var(--text-gray);
  line-height: 1.6;
  text-align: center;
  transition: color 0.3s ease;
}

.work-item-placeholder:hover p {
  color: var(--text-dark);
}

.view-project-button {
  display: inline-flex;
  align-items: center;
  padding: 12px 24px;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--primary-color);
  background-color: var(--primary-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-decoration: none;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
}

.view-project-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: all 0.6s ease;
}

.view-project-button:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(74, 108, 253, 0.3);
}

.view-project-button:hover::before {
  left: 100%;
}

.work-item-placeholder .view-project-button {
  margin: 10px auto;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.work-item-placeholder:hover .view-project-button {
  opacity: 1;
  transform: translateY(0);
}

/* Contact Section - Enhanced */
.contact-section {
  background-color: var(--bg-light);
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  display: block;
  border-top: 1px solid var(--border-light);
  margin-top: 0;
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  perspective: 1000px;
}

/* Add subtle background pattern to contact section */
.contact-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 300px;
  background: var(--primary-lighter);
  border-radius: 30% 70% 50% 50% / 30% 30% 70% 70%;
  opacity: 0.6;
  z-index: 0;
  animation: floatAnimation 20s infinite alternate ease-in-out;
}

.contact-section > p {
  font-size: 1.2rem;
  color: var(--text-gray);
  max-width: 700px;
  margin: 0 auto 50px;
  text-align: center;
  position: relative;
  z-index: 1;
  line-height: 1.8;
}

/* Contact Info Layout */
.contact-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  position: relative;
  z-index: 1;
  margin: 0 auto;
  max-width: 1200px;
  transform-style: preserve-3d;
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100%;
  justify-content: space-between;
}

.contact-details .detail-item {
  min-height: 90px;
  display: flex;
  align-items: center;
  padding: 20px;
  background: var(--bg-white);
  border-radius: 12px;
  box-shadow: var(--shadow-sm);
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  border: 1px solid var(--border-light);
  position: relative;
  overflow: hidden;
  transform-origin: left center;
}

.contact-details .detail-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background: var(--primary-gradient);
  transform: translateX(-5px);
  transition: transform 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.contact-details .detail-item::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(74, 108, 253, 0.05), transparent);
  opacity: 0;
  transition: opacity 0.5s ease;
  z-index: -1;
}

.contact-details .detail-item:hover {
  transform: translateX(8px) scale(1.02);
  box-shadow: var(--shadow-md);
  border-color: var(--primary-light);
}

.contact-details .detail-item:hover::before {
  transform: translateX(0);
}

.contact-details .detail-item:hover::after {
  opacity: 1;
}

.detail-icon {
  width: 50px;
  height: 50px;
  background: var(--primary-light);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
}

.detail-icon::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--primary-gradient);
  opacity: 0;
  transition: opacity 0.5s ease;
}

.contact-details .detail-item:hover .detail-icon {
  transform: scale(1.1) rotate(5deg);
}

.contact-details .detail-item:hover .detail-icon::before {
  opacity: 1;
}

.detail-icon svg {
  width: 24px;
  height: 24px;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: relative;
  z-index: 1;
}

.contact-details .detail-item:hover .detail-icon svg {
  fill: white;
  transform: scale(1.1);
}

.contact-details h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-dark);
  margin: 0 0 5px;
  transition: color 0.3s ease;
}

.contact-details .detail-item:hover h4 {
  color: var(--primary-color);
}

.contact-details p {
  font-size: 1rem;
  color: var(--text-gray);
  margin: 0;
  transition: color 0.3s ease;
}

.contact-details .detail-item:hover p {
  color: var(--text-dark);
}

/* Contact form - Match width with contact details */
.contact-form {
  background: var(--bg-white);
  padding: 40px;
  border-radius: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-light);
  display: flex;
  flex-direction: column;
  gap: 25px;
  transition: all 0.5s ease;
  height: 100%;
  justify-content: space-between;
  transform-origin: right center;
}

.contact-form:hover {
  box-shadow: var(--shadow-lg);
  border-color: var(--primary-light);
  transform: scale(1.01);
}

.form-group {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
  min-height: 60px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 18px;
  border: 1px solid var(--border-light);
  border-radius: 12px;
  background: var(--bg-light);
  font-size: 1rem;
  color: var(--text-dark);
  transition: all 0.4s ease;
  resize: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.02) inset;
  position: relative;
  z-index: 1;
}

/* Enhanced input styles to match contact info */
.contact-form input:hover,
.contact-form textarea:hover {
  border-color: var(--primary-light);
  transform: translateX(5px);
  background-color: rgba(255, 255, 255, 0.9);
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 4px var(--primary-light), 0 2px 5px rgba(0, 0, 0, 0.02) inset;
  transform: translateX(8px);
  background-color: white;
}

/* Add decoration to form fields */
.form-group::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background: var(--primary-gradient);
  transform: translateX(-5px);
  opacity: 0;
  transition: transform 0.5s cubic-bezier(0.25, 0.8, 0.25, 1), opacity 0.3s ease;
  border-radius: 3px;
  z-index: 0;
}

.form-group:hover::before {
  transform: translateX(0);
  opacity: 1;
}

.contact-form input:invalid:not(:placeholder-shown),
.contact-form textarea:invalid:not(:placeholder-shown) {
  border-color: red;
}

.contact-form input:invalid:not(:placeholder-shown) + .error-message,
.contact-form textarea:invalid:not(:placeholder-shown) + .error-message {
  display: block;
  opacity: 1;
}

.error-message {
  display: none;
  position: absolute;
  bottom: -20px;
  left: 0;
  color: #e74c3c;
  font-size: 0.85rem;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.contact-form textarea {
  min-height: 180px;
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
}

/* Enhanced send button */
.contact-form button {
  width: auto;
  min-width: 180px;
  align-self: center;
  padding: 0 40px;
  font-size: 1.05rem;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  height: 60px;
  background: var(--primary-gradient);
  border: none;
  color: white;
  font-weight: 600;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  transform-origin: center;
  letter-spacing: 0.5px;
}

.contact-form button::after {
  content: '';
  position: absolute;
  width: 120%;
  height: 100%;
  top: 0;
  left: -120%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: all 0.8s ease;
  transform: skewX(-25deg);
  z-index: 1;
}

.contact-form button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 10px 20px rgba(74, 108, 253, 0.4);
}

.contact-form button:hover::after {
  left: 120%;
}

.contact-form button span {
  position: relative;
  z-index: 2;
}

.form-submit-message {
  min-height: 60px;
}

.success-message, .error-submit-message {
  border-radius: 8px;
  padding: 0;
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 0.95rem;
  text-align: center;
}

.success-message {
  background-color: rgba(39, 174, 96, 0.1);
  color: #27ae60;
  border: 1px solid rgba(39, 174, 96, 0.3);
}

.error-submit-message {
  background-color: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.3);
}

/* Enhanced Footer */
footer {
  background-color: var(--bg-white);
  padding: 80px 0 40px;
  position: relative;
  overflow: hidden;
  margin-top: 0;
}

footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 10% 90%, var(--primary-lighter) 0%, transparent 25%),
    radial-gradient(circle at 90% 10%, var(--primary-lighter) 0%, transparent 20%);
  z-index: 0;
  opacity: 0.7;
}

/* Enhanced footer with moving particles */
footer::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 40%, var(--primary-light) 0%, transparent 8%),
    radial-gradient(circle at 80% 60%, var(--primary-light) 0%, transparent 8%),
    radial-gradient(circle at 40% 80%, var(--primary-light) 0%, transparent 6%),
    radial-gradient(circle at 60% 30%, var(--primary-light) 0%, transparent 6%);
  z-index: 0;
  opacity: 0.4;
  animation: footerParticles 20s infinite alternate ease-in-out;
}

@keyframes footerParticles {
  0% { background-position: 0% 0%, 0% 0%, 0% 0%, 0% 0%; }
  100% { background-position: 5% 10%, -5% -10%, 8% -8%, -8% 8%; }
}

.footer-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

.footer-logo {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-dark);
  position: relative;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.05);
  animation: glowLogo 3s infinite alternate ease-in-out;
}

@keyframes glowLogo {
  0% { text-shadow: 0 2px 10px rgba(0, 0, 0, 0.05); }
  100% { text-shadow: 0 2px 20px rgba(74, 108, 253, 0.2); }
}

.footer-logo::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 4px;
  background: var(--primary-gradient);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
  box-shadow: 0 2px 10px rgba(74, 108, 253, 0.3);
}

.footer-social {
  display: flex;
  gap: 20px;
  margin: 20px 0;
}

.footer-social a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--bg-light);
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: relative;
  overflow: hidden;
}

.footer-social a::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--primary-gradient);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 0;
}

.footer-social a:hover {
  transform: translateY(-5px) rotate(10deg);
  box-shadow: var(--shadow-sm);
}

.footer-social a:hover::before {
  opacity: 1;
}

.footer-social svg {
  width: 20px;
  height: 20px;
  fill: var(--primary-color);
  position: relative;
  z-index: 1;
  transition: fill 0.3s ease;
}

.footer-social a:hover svg {
  fill: white;
}

.footer-links {
  display: flex;
  gap: 30px;
  margin-bottom: 40px;
  flex-wrap: wrap;
  justify-content: center;
}

.footer-links a {
  color: var(--text-gray);
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  padding: 3px 0;
}

.footer-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--primary-gradient);
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.footer-links a:hover {
  color: var(--primary-color);
}

.footer-links a:hover::after {
  width: 100%;
}

.footer-bottom {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 30px;
  border-top: 1px solid var(--border-light);
}

footer p {
  color: var(--text-gray);
  font-size: 0.9rem;
  margin: 0;
  position: relative;
  overflow: hidden;
  padding: 5px 10px;
  transition: all 0.3s ease;
}

footer p:hover {
  color: var(--primary-color);
}

/* Section transitions for smooth scrolling */
section {
  transition: opacity 0.5s ease, transform 0.5s ease;
  position: relative;
  overflow: hidden;
}

/* Section decoration - common elements */
section::before,
section::after {
  content: "";
  pointer-events: none;
}

/* Section highlight effect when targeted */
section:target {
  animation: sectionHighlight 1.5s ease;
}

@keyframes sectionHighlight {
  0% { background-color: transparent; }
  25% { background-color: rgba(74, 108, 253, 0.05); }
  100% { background-color: transparent; }
}

/* Fix responsive issues for larger screens */
@media (min-width: 1400px) {
  .hero-content {
    max-width: 1400px;
    padding: 0 50px;
  }
  
  .hero-content-left h1 {
    font-size: 4.5rem;
  }
  
  .profile-card {
    width: 420px;
  }
  
  .profile-image-container {
    height: 420px;
  }
}

/* Fix responsive issues for medium screens */
@media (max-width: 992px) {
  .hero-section {
    padding: 120px 0 80px;
  }
  
  .hero-content {
    flex-direction: column;
    gap: 50px;
  }
  
  .hero-content-left {
    max-width: 100%;
    text-align: center;
    order: 1;
  }
  
  .hero-content-left h1 {
    font-size: 3.5rem;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .social-icons {
    justify-content: center;
  }
  
  .hero-content-right {
    order: 2;
    margin: 0 auto;
  }
  
  .contact-info {
    grid-template-columns: 1fr;
    gap: 40px;
    max-width: 600px;
  }
  
  .about-text h3 {
    text-align: center;
  }
  
  .about-text p {
    text-align: center;
  }
  
  .about-text .about-cta {
    margin: 20px auto 0;
    display: block;
    width: fit-content;
  }
  
  .contact-form textarea {
    min-height: 150px;
  }
}

/* Fix responsive issues for small screens */
@media (max-width: 768px) {
  section, footer {
    padding: 70px 0;
  }
  
  .about-text h3 {
    font-size: 1.5rem;
  }
  
  .about-text p {
    font-size: 1rem;
  }
  
  .skill-card h3 {
    font-size: 1.3rem;
  }
  
  .skill-card li {
    font-size: 0.95rem;
  }
  
  .work-item-placeholder h3 {
    font-size: 1.2rem;
  }
  
  .work-item-placeholder p {
    font-size: 0.95rem;
  }
  
  .contact-form {
    padding: 30px;
  }
  
  .contact-details .detail-item {
    padding: 15px;
  }
  
  .detail-icon {
    width: 40px;
    height: 40px;
  }
  
  .contact-details h4 {
    font-size: 1rem;
  }
  
  .contact-details p {
    font-size: 0.9rem;
  }
  
  .footer-logo {
    font-size: 2rem;
  }
  
  .footer-links {
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }
}

/* Fix responsive issues for small screens */
@media (max-width: 480px) {
  section, footer {
    padding: 60px 0;
  }
  
  .hero-section {
    padding: 100px 0 60px;
  }
  
  .hero-content-left h1 {
    font-size: 2.8rem;
  }
  
  .hero-content-left h1 .profession {
    font-size: 2.4rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    gap: 15px;
    width: 100%;
  }
  
  .primary-button, .secondary-button {
    width: 100%;
    font-size: 0.9rem;
    padding: 12px 30px;
  }
  
  .profile-card {
    width: 100%;
  }
  
  .profile-image-container {
    height: 320px;
  }
  
  .about-section h2, 
  .skills-section h2, 
  .works-section h2, 
  .contact-section h2 {
    font-size: 2rem;
    margin-bottom: 40px;
  }
  
  .about-section h2::after, 
  .skills-section h2::after, 
  .works-section h2::after, 
  .contact-section h2::after {
    width: 60px;
    height: 4px;
    bottom: -12px;
  }
  
  .about-text h3 {
    font-size: 1.3rem;
  }
  
  .skill-card {
    padding: 20px;
  }
  
  .skills-container {
    gap: 20px;
  }
  
  .works-grid {
    gap: 30px;
  }
  
  .work-item-image-placeholder {
    height: 180px;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 12px;
  }
  
  .error-message {
    bottom: -18px;
    font-size: 0.75rem;
  }
  
  .success-message, .error-submit-message {
    font-size: 0.85rem;
  }
  
  .contact-details .detail-item {
    padding: 12px;
  }
  
  .detail-icon {
    width: 36px;
    height: 36px;
    margin-right: 15px;
  }
  
  .footer-social a {
    width: 36px;
    height: 36px;
  }
  
  .footer-links {
    gap: 15px;
  }
  
  .footer-links a {
    font-size: 0.9rem;
  }
  
  footer p {
    font-size: 0.8rem;
  }
