/* Scroll Animation Classes */
.fade-in-section {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s cubic-bezier(0.34, 1.56, 0.64, 1), 
              transform 1s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}

.fade-in-section.is-visible {
  opacity: 1;
  transform: translateY(0);
}

.slide-in-right {
  opacity: 0;
  transform: translateX(30px);
  transition: opacity 1s cubic-bezier(0.34, 1.56, 0.64, 1), 
              transform 1s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}

.slide-in-right.is-visible {
  opacity: 1;
  transform: translateX(0);
}

.slide-in-left {
  opacity: 0;
  transform: translateX(-30px);
  transition: opacity 1s cubic-bezier(0.34, 1.56, 0.64, 1), 
              transform 1s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}

.slide-in-left.is-visible {
  opacity: 1;
  transform: translateX(0);
}

.scale-in {
  opacity: 0;
  transform: scale(0.9) translateY(30px);
  transition: opacity 0.8s cubic-bezier(0.34, 1.56, 0.64, 1), 
              transform 0.8s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}

.scale-in.is-visible {
  opacity: 1;
  transform: scale(1) translateY(0);
}

/* Centered headings for skills and works items */
.skill-card h3,
.work-item-placeholder h3 {
  text-align: center;
}

.skill-card h3::after,
.work-item-placeholder h3::after {
  left: 50%;
  transform: translateX(-50%);
}

.skill-card ul {
  padding-top: 10px;
}

.work-item-placeholder p {
  text-align: center;
}

.work-item-placeholder .view-project-button {
  margin: 20px auto;
}

/* Center work item details */
.work-item-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
}

/* Skill card centered styles */
.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 35px 25px;
}

.skill-card h3 {
  margin-bottom: 30px;
}

.skill-card ul {
  width: 100%;
  text-align: left;
  padding-left: 20px;
}

.skill-card li {
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
}

.skill-card li::before {
  left: 0;
}

/* Nicer spacing in about section */
.about-section > * {
  max-width: 900px;
}

.about-text p {
  margin-bottom: 25px;
  padding: 0 15px;
}

/* Restore missing hero styles */
.hero-content-left h1 .greeting {
  font-weight: 400;
  color: var(--text-gray);
}

.hero-content-left h1 .name-highlight {
  color: var(--primary-color);
  position: relative;
  display: inline-block;
}

.hero-content-left h1 .name-highlight::after {
  content: '';
  position: absolute;
  width: 110%;
  height: 30%;
  bottom: 5px;
  left: -5%;
  background-color: var(--primary-light);
  z-index: -1;
  transform: rotate(-2deg);
}

.hero-content-left h1 .profession {
  font-size: 3.5rem;
  color: var(--text-dark);
}

/* Project View Button - Enhanced */
.view-project-button {
  display: inline-flex;
  align-items: center;
  padding: 10px 20px;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary-color);
  background-color: var(--primary-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-decoration: none;
  margin-top: 15px;
  position: relative;
  overflow: hidden;
}

.view-project-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: all 0.6s ease;
}

.view-project-button:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(74, 108, 253, 0.3);
}

.view-project-button:hover::before {
  left: 100%;
}

