@keyframes float-bubble {
  0% {
    transform: translateY(100vh) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: 0.2;
  }
  20% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.3;
    transform: translateY(50vh) translateX(10px) scale(0.8);
  }
  80% {
    opacity: 0.5;
    transform: translateY(10vh) translateX(-10px) scale(1);
  }
  100% {
    transform: translateY(-100px) scale(1.2);
    opacity: 0;
  }
}

